
.contact {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f9f9f9;
}

.contactWrapper {
  width: 60%;
  padding: 20px;
  background-color: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
}

.contactTitle {
  text-align: center;
  margin-bottom: 20px;
  font-size: 2rem;
  color: teal;
}

.contactContent {
  display: flex;
}

.contactImageWrapper {
  flex: 1;
  background-image: url('../../assets/images/camera-1130731_1280.jpg');
  background-size: cover;
  background-position: center;
  border-radius: 10px;
}

.contactForm {
  flex: 2;
  display: flex;
  flex-direction: column;
  margin-left: 20px;
}

.contactForm > label {
  margin: 10px 0 5px;
  font-size: 1.1rem;
  color: #333;
}

.contactForm > input, .contactForm > textarea {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 20px;
}

.contactForm > input[readonly] {
  background-color: #f9f9f9;
  color: #333;
}

.contactForm > textarea {
  resize: none;
  height: 100px;
}
