.header {
  margin-top: 60px;
  position: relative; /* 추가: 배경 이미지가 있는 요소 안에 텍스트를 올바르게 배치하기 위해 필요 */
  height: 450px; /* 이미지와 텍스트의 높이를 맞추기 위해 추가 */
  background-image: url('/src/assets/images/tree-7739243_1280.jpg'); /* 절대 경로로 배경 이미지 설정 */
  background-size: cover;
  background-position: center;
}

.headerTitles {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Lora", serif;
  color: #fff;
  position: absolute; /* 추가: 텍스트가 배경 이미지 위에 위치하도록 설정 */
  width: 100%;
  top: 10%; 
  transform: translateY(-50%); /* 중앙에 배치 */
}

.headerTitleSm {
  font-size: 20px;
}

.headerTitleLg {
  font-size: 100px;
}
